import { JobEvent, JobStatus, ListedJob } from "@common/api/v1.1/types/jobs";
import { State } from "../types";

export interface UpdateJobsAction {
  blockingStep: string;
  eventType: JobEvent | null;
  jobs: Array<ListedJob>;
  type: "UPDATE JOBS";
}

export const updateJobs = (state: State, action: UpdateJobsAction): State => {
  if (action.jobs.length < 1) {
    return state;
  }
  if (action.eventType === null) {
    return {
      ...state,
      jobs: action.jobs.reduce((prev: State["jobs"], job: ListedJob): State["jobs"] => {
        prev[job.id] = {
          data: job,
          override: undefined,
        };
        return prev;
      }, {}),
    };
  }
  let jobs = state.jobs;
  switch (action.eventType) {
    case JobEvent.JobBlocked:
    case JobEvent.JobCheckedIn:
    case JobEvent.JobCheckedOut:
    case JobEvent.JobCollaborationChanged:
    case JobEvent.JobCreated:
    case JobEvent.JobProgressChanged:
    case JobEvent.JobStatusChanged:
      jobs = action.jobs.reduce((prev: State["jobs"], job: ListedJob): State["jobs"] => {
        if (job.status === JobStatus.BLOCKED && job.blocking_step === action.blockingStep) {
          const newJobData = {
            data: job,
            override: state.jobs[job.id]?.override,
          };
          if (prev === state.jobs) {
            return {
              ...state.jobs,
              [job.id]: newJobData,
            };
          }
          prev[job.id] = newJobData;
        } else if (job.id in prev) {
          let toReturn = prev;
          if (prev === state.jobs) {
            toReturn = { ...prev };
          }
          delete toReturn[job.id];
          return toReturn;
        }
        return prev;
      }, state.jobs);
      break;
    case JobEvent.JobCancelled:
    case JobEvent.JobDeleted:
    case JobEvent.JobFailed:
      jobs = action.jobs.reduce((prev: State["jobs"], job: ListedJob): State["jobs"] => {
        if (!(job.id in prev)) {
          return prev;
        }
        if (prev === state.jobs) {
          const toReturn = { ...prev };
          delete toReturn[job.id];
          return toReturn;
        }
        delete prev[job.id];
        return prev;
      }, state.jobs);
      break;
  }
  if (jobs === state.jobs) {
    return state;
  }
  return {
    ...state,
    jobs,
  };
};
